import React, { Component } from 'react'
import * as THREE from 'three'
import Wave from './wave'

let camera, scene, renderer, controls

export default class GLView extends Component {
  componentDidMount = () => {
    this.view = new Wave()

    this.setupCamera()
    this.setupRenderer()
    this.setupScene()
    this.setupLights()
    this.animate()
  }

  setupCamera = () => {
    const { innerWidth: width, innerHeight: height } = window

    scene = new THREE.Scene()
    camera = new THREE.OrthographicCamera(
      width / -2,
      width / 2,
      height / 2,
      height / -2,
      10,
      10000
    )
    camera.zoom = this.props.zoom
    camera.updateProjectionMatrix()
  }

  setupRenderer = () => {
    const { innerWidth: width, innerHeight: height } = window

    renderer = new THREE.WebGLRenderer({
      alpha: true,
      antialias: true,
      clearAlpha: 0.25
    })
    renderer.setClearColor(0xebebeb)
    renderer.setPixelRatio(Math.max(window.devicePixelRatio, 1.0))
    renderer.setSize(width, height)
    this.refs.container.appendChild(renderer.domElement)
  }

  setupScene = () => {
    scene.add(this.view.group)
  }

  setupLights = () => {
    let aLight = new THREE.AmbientLight(0xebebeb)
    let dLight = new THREE.DirectionalLight(0x6695f7, 1, 100)
    dLight.position.set(0, 0, 100)
    // scene.add(aLight)
    // scene.add(dLight)
  }

  animate = () => {
    this.view.update()
    requestAnimationFrame(this.animate)
    renderer.render(scene, camera)
  }

  render() {
    return <div id="glView" ref="container" className="slow-fade" />
  }
}



